import React, { useState, useEffect } from "react";
import { v4 } from 'uuid'
import { randomColor } from 'randomcolor'
import Draggable from "react-draggable";

function App() {
  const [item, setItem] = useState('')
  const [items, setItems] = useState(JSON.parse(localStorage.getItem('items')) || []) //
  console.log(items);
  useEffect(() => {
    localStorage.setItem('items', JSON.stringify(items))
  }, [items])

  const newItem = () => {
    if (item.trim() !== '') {
      const newItem = {
        id: v4(),
        item: item,
        color: randomColor({
          luminosity: 'light',
        }),
        defPos: {
          x: newRandomX(),
          y: newRandomY()
        }
      }
      setItems((items => [...items, newItem]))
      setItem('')
    } else {
      alert('enter something')
    }
  }

  function newRandomX() {
    return Math.floor(Math.random() * (1500 - 200 + 1)) + 200
  }

  function newRandomY() {
    return Math.floor(Math.random() * (-800 - (-1000) + 1)) - 800
  }
  console.log(newRandomX());
  console.log(newRandomY());
  const dellItem = (id) => {
    setItems(items.filter((item) => item.id !== id))
  }

  const updataPosition = (data, index) => {
    let temparr = [...items]
    temparr[index].defPos = { x: data.x, y: data.y }
    setItems(temparr)
  }

  return <div className="App">
    <h1>Local storage enabled. Try f5</h1>
    <div className="wrapper">
      <input
        value={item}
        type="text"
        placeholder="Enter something..."
        onChange={(e) => setItem(e.target.value)}
      />
      <button className="enter" onClick={newItem}>ENTER</button>
    </div>
    {
      items.map((e, index) => {
        return <Draggable key={index} defaultPosition={e.defPos} onStop={(e, data) => updataPosition(data, index)}>
          <div className="todo_item" style={{ backgroundColor: e.color }} >
            {`${e.item}`}1
            <button className="delite" onClick={() => dellItem(e.id)}>X</button>
          </div>
        </Draggable>
      })
      /*
 
      */
    }
  </div >
}

export default App;
